/* src/components/Loading.js 파일 생성 */
import React from "react";
import styled from "styled-components";

const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
`;
const Text = styled.span`
  font-size: 24px;
  font-weight: 600;
  color: grey;
`;

function Loading() {
  return (
    <Container>
      <Text>Loading...</Text>
    </Container>
  );
}

export default Loading;
