import React, { useState, useEffect } from "react";
import styled from "styled-components";
import PropTypes from "prop-types";

import Loading from "../components/Loading";

const Container = styled.div`
  max-width: 70%;
  min-width: 400px;
  margin: 0 auto;
  margin-top: 100px;
  margin-bottom: 100px;
  padding: 20px;
  border-radius: 5px;
  background-color: white;
  font-weight: 300;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  display: flex;
`;
const Poster = styled.img`
  min-width: 200px;
  max-width: 250px;
  min-height: 300px;
  max-height: 350px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
`;

const MovieData = styled.div``;

const Title = styled.div`
  color: #2c2c2c;
  font-size: 24px;
  margin-bottom: 5px;
`;

const Year = styled.div`
  color: #adaeb9;
  margin-top: 10px;
  font-size: 14px;
`;

const Genres = styled.div`
  color: #adaeb9;
  margin-top: 10px;
  font-size: 14px;
`;

const Genre = styled.span`
  margin-right: 10px;
`;

const Description = styled.div`
  color: #adaeb9;
  margin-top: 20px;
`;

function Detail({ location, match, history, API }) {
  const [movie, setMovie] = useState(location.state);

  useEffect(() => {
    const getMovie = async () => {
      if (isNaN(Number(match.params.id))) {
        history.replace("/error");
      } else if (movie === undefined) {
        try {
          const result = await API.get(
            `/movie_details.json?movie_id=${match.params.id}`
          );
          if (result.data.data.movie.title === null) {
            history.replace("/error");
          } else {
            const data = result.data.data.movie;
            setMovie({
              title: data.title,
              year: data.year,
              description: data.description_full,
              poster: data.medium_cover_image,
              genres: data.genres
            });
          }
        } catch {
          history.push("/error");
        }
      }
    };
    getMovie();
  }, [match, history, API, movie, setMovie]);

  if (movie === undefined) {
    return <Loading />;
  } else {
    const { title, year, description, poster, genres } = movie;
    return (
      <Container>
        <Poster src={poster} />
        <MovieData>
          <Title>{title}</Title>
          <Year>{year}</Year>
          <Genres>
            {genres.map((genre, index) => (
              <Genre key={index}>{genre}</Genre>
            ))}
          </Genres>
          <Description>{description}</Description>
        </MovieData>
      </Container>
    );
  }
}

export default Detail;

Detail.propTypes = {
  location: PropTypes.object.isRequired,
  match: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  API: PropTypes.func.isRequired
};
